.ui.label.customer-label {
  margin-bottom: 0.33rem;
}

.a-like-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: none;
  display: inline;
  margin: 0;
  padding: 0;
}

.a-like-button:hover,
.a-like-button:focus {
  text-decoration: none;
}

.ui.form textarea.no-border-textarea {
  border: none;
}

.ui.form textarea.no-resize-textarea {
  resize: none;
}
